import React, { useRef, useState } from "react";
import indiastates from "../data/indiaStates.json";
import EarthquakeStations from "../data/EarthquakeStations.json";
import MajorEarthquakes from "../data/MajorEarthquakes.json";
import EarthquakeZones from "../data/EarthquakeZones.json";
import EarthquakeFaults from "../data/EarthquakeFaults.json";
import { setWindowWidth } from "../utilities/Functions";
import { setInitialMapZoom } from "../utilities/Functions";
import { setDragging } from "../utilities/Functions";

import {
    MapContainer,
    useMapEvents,
    GeoJSON,
    FeatureGroup,
    ScaleControl,
    Circle,
    Tooltip,
    Pane,
} from "react-leaflet";
import "leaflet/dist/leaflet.css";
import "leaflet-fullscreen/dist/Leaflet.fullscreen.js";
import "leaflet-fullscreen/dist/leaflet.fullscreen.css";
import ExportMapButton from "../components/ExportMapButton";
import LayerControlBtn from "../components/LayerControlBtn";


const Legend = ({ baseLayers }) => {
    const EarthquakeZonesLegend = [
        { color: "skyblue", label: "Zone II: Low Damage Risk Zone" },
        { color: "yellow", label: "Zone III: Moderate Damage Zone" },
        { color: "orange", label: "Zone IV: High Damage Risk Zone" },
        { color: "red", label: "Zone V: Very High Damage Risk Zone" },
    ];
    const EarthquakeFaultsLegend = [{ color: "blue", label: "Earthquake Faults" }];
    const EarthquakeStationsLegend = [{ color: "brown", label: "Earthquake Stations" }];
    const MajorEarthquakesLegend = [{ color: "green", label: "Major Earthquakes" }];

    return (
        <>
            {baseLayers.length > 0 && (
                <div className="legend_container">
                    <h5>Legends</h5>
                    {baseLayers.includes("EarthquakeZones") && (
                        <div>
                            {EarthquakeZonesLegend.map((item) => (
                                <div key={item.color} className="legend_item">
                                    <span
                                        className="legend_item_square"
                                        style={{ backgroundColor: item.color }}
                                    />
                                    <span className="legend-label">{item.label}</span>
                                </div>
                            ))}
                        </div>
                    )}
                    {baseLayers.includes("EarthquakeFaults") && (
                        <div>
                            {EarthquakeFaultsLegend.map((item) => (
                                <div key={item.color} className="legend_item">
                                    <span
                                        className="legend_item_square"
                                        style={{ backgroundColor: item.color }}
                                    />
                                    <span className="legend-label">{item.label}</span>
                                </div>
                            ))}
                        </div>
                    )}
                    {baseLayers.includes("EarthquakeStations") && (
                        <div>
                            {EarthquakeStationsLegend.map((item) => (
                                <div key={item.color} className="legend_item">
                                    <span
                                        className="legend_item_square"
                                        style={{ backgroundColor: item.color }}
                                    />
                                    <span className="legend-label">{item.label}</span>
                                </div>
                            ))}
                        </div>
                    )}
                    {baseLayers.includes("MajorEarthquakes") && (
                        <div>
                            {MajorEarthquakesLegend.map((item) => (
                                <div key={item.color} className="legend_item">
                                    <span
                                        className="legend_item_square"
                                        style={{ backgroundColor: item.color }}
                                    />
                                    <span className="legend-label">{item.label}</span>
                                </div>
                            ))}
                        </div>
                    )}

                </div>
            )}
        </>
    );
};

const EarthquakeMap = () => {
    const [mousePosition, setMousePosition] = useState({ lat: 0, lng: 0 });
    const mapContainerRef = useRef(null);

    const HandleMouseHover = () => {
        useMapEvents({
            mousemove: (e) => {
                setMousePosition(e.latlng);
            },
        });

        return null;
    };

    // function onEachFeatureDistrict(feature, layer) {
    //     layer.on("mouseover", function (e) {
    //         if (feature.properties && feature.properties.District) {
    //             const popupContent = `
    //       <div>
    //         District: ${feature.properties.District}<br/>
    //         STATE: ${feature.properties.STATE}
    //       </div>
    //     `;
    //             layer.bindTooltip(popupContent);
    //         }
    //         layer.openTooltip();
    //     });

    //     layer.on("mouseout", function () {
    //         layer.closeTooltip();
    //     });
    // }

    const earthquakeZonesDensity = (ZONE) => {
        return ZONE === "ZONE II: LOW DAMAGE RISK ZONE"
            ? "skyblue"
            : ZONE === "ZONE III: MODERATE DAMAGE ZONE"
                ? "yellow"
                : ZONE === "ZONE IV: HIGH DAMAGE RISK ZONE"
                    ? "orange"
                    : ZONE === "ZONE V: VERY HIGH DAMAGE RISK ZONE"
                        ? "red"
                        : "black";
    };
    const earthquakeZonesStyle = (feature) => {
        return {
            fillColor: earthquakeZonesDensity(feature.properties.ZONE),
            weight: 1,
            opacity: 1,
            color: "black",
            dashArray: '2',
            fillOpacity: 1,
            interactive: false,
        };
    };

    const [baseLayers, setBaseLayers] = useState([
        "EarthquakeZones",
        "EarthquakeFaults",
        "EarthquakeStations",
        "MajorEarthquakes"

    ]);


    const layersItems = [
        { value: "EarthquakeZones", label: "Earthquake Zones" },
        { value: "EarthquakeFaults", label: "Earthquake Faults" },
        { value: "MajorEarthquakes", label: "Major Earthquakes" },
        { value: "EarthquakeStations", label: "Earthquake Stations" },
    ];

    const [singlebaseLayer, setSingleBaseLayer] = useState("");
    const singleLayerItems = [



    ];

    return (
        <div className="main_page_container">
            <div className="row">
                <div className="col-md-3">
                    <div className="info_container">
                        <h3>Earthquake</h3>
                        <p>
                            India, located in a seismically active zone, is prone to
                            earthquakes. These sudden and violent shaking of the ground occur
                            due to the movement of tectonic plates beneath the Earth's
                            surface. Earthquakes can vary in magnitude and intensity, and
                            their impact depends on factors such as depth, location, and
                            population density. In India, regions like the Himalayas, the
                            northeastern states, and parts of Gujarat are particularly
                            susceptible to earthquakes. To minimize the damage caused by
                            earthquakes, India has implemented measures like seismic zoning,
                            construction guidelines for earthquake-resistant buildings, and
                            public awareness campaigns to educate people on safety protocols
                            during seismic events.
                        </p>
                    </div>
                </div>

                <div className="col-md-9">
                    <div className="map_container" ref={mapContainerRef}>
                        <MapContainer
                            center={[22, 84]}
                            fullscreenControl={true}
                            zoom={setInitialMapZoom()}
                            style={setWindowWidth()}
                            attributionControl={false}
                            scrollWheelZoom={false}
                            zoomDelta={0.25}
                            zoomSnap={0}
                            minZoom={3}
                            keyboard={false}
                            dragging={setDragging()}
                            // touchZoom={false}
                            doubleClickZoom={false}
                            maxZoom={9}
                        >
                            <HandleMouseHover />
                            <ScaleControl />
                            <Pane name="indiastates_pane" style={{ zIndex: -3 }}>
                            <GeoJSON
                                data={indiastates.features}
                                style={{
                                    fillColor: "#84857e",
                                    fillOpacity: 0.5,
                                    weight: 2,
                                    color: "whitesmoke",
                                    interactive: false,
                                }}
                            />
                            </Pane>
                            <LayerControlBtn setBaseLayers={setBaseLayers} baseLayers={baseLayers} layersItems={layersItems} singlebaseLayer={singlebaseLayer} setSingleBaseLayer={setSingleBaseLayer} singleLayerItems={singleLayerItems}/>


                            <Pane name="EarthquakeZones_pane" style={{ zIndex: -2 }}>
                            {baseLayers.includes("EarthquakeZones") && (
                                <GeoJSON
                                    data={EarthquakeZones.features}
                                    style={earthquakeZonesStyle}
                                />
                            )}
                            </Pane>
                            {baseLayers.includes("EarthquakeFaults") && (
                                <GeoJSON
                                    data={EarthquakeFaults.features}
                                    style={{
                                        fillColor: "none",
                                        weight: 2,
                                        color: "blue",
                                        interactive: false,
                                    }}
                                />
                            )}

                            {baseLayers.includes("MajorEarthquakes") && (
                                <FeatureGroup>
                                    {MajorEarthquakes.features.map((station, index) => {
                                        const magnitude = station.properties.MAGNITUDE;

                                        // const minMagnitude = 6;
                                        // const maxMagnitude = 8;
                                        // const minRadius = 1000;
                                        // const maxRadius = 5000;
                                        // const radius = ((magnitude - minMagnitude) / (maxMagnitude - minMagnitude)) * (maxRadius - minRadius) + minRadius;
                                        const radius = ((magnitude - 5) / 2) * 40000;

                                        return (
                                            <Circle
                                                key={index}
                                                center={[
                                                    station.geometry.coordinates[1],
                                                    station.geometry.coordinates[0],
                                                ]}
                                                radius={radius}
                                                color="green"
                                                fillColor="green"
                                                fillOpacity={1}
                                            >
                                                <Tooltip sticky>
                                                    <div className="popup_content">
                                                        <h6>Major Earthquake</h6>
                                                        <p>DATE: {station.properties.DATE}</p>
                                                        <p>LOCATION: {station.properties.LOCATION}</p>
                                                        <p>MAGNITUDE: {station.properties.MAGNITUDE}</p>
                                                    </div>
                                                </Tooltip>
                                            </Circle>
                                        );
                                    })}
                                </FeatureGroup>
                            )}

                            {baseLayers.includes("EarthquakeStations") && (
                                <FeatureGroup>
                                    {EarthquakeStations.features.map((station, index) => {
                                        return (
                                            <Circle
                                                key={index}
                                                center={[
                                                    station.geometry.coordinates[1],
                                                    station.geometry.coordinates[0],
                                                ]}
                                                radius={1800}
                                                color="brown"
                                                fillColor="brown"
                                            >
                                                <Tooltip sticky>
                                                    <div className="popup_content">
                                                        <h6>Earthquake Station</h6>
                                                        <p>STATION: {station.properties.STATION}</p>
                                                        <p>CODE: {station.properties.CODE}</p>
                                                        <p>STATE_UT: {station.properties.STATE_UT}</p>
                                                    </div>
                                                </Tooltip>
                                            </Circle>
                                        );
                                    })}
                                </FeatureGroup>
                            )}


                            <ExportMapButton
                                mapContainerRef={mapContainerRef}
                                mapTitle="Natural Disaster: Earthquake"
                            />

                            <div className="desktop_legend_container">
                                {baseLayers && <Legend baseLayers={baseLayers} />}
                            </div>

                            <div className="coordinate_container">
                                Lat: {mousePosition.lat.toFixed(4)}, Long:{" "}
                                {mousePosition.lng.toFixed(4)}
                            </div>
                        </MapContainer>
                        <div className="phone_legend_container">
                            {baseLayers && <Legend baseLayers={baseLayers} />}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default EarthquakeMap;
